@import url(https://fonts.googleapis.com/css?family=Crete+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#Footer{background-color:#212121;padding:30px 0px 20px 0px;display:flex;justify-content:center;align-items:center;flex-direction:column}#Footer p{color:white}#Footer p .red{color:red}#Footer p a{text-decoration:none;color:#16c9ff}#Footer #gt img{width:50px;margin-top:30px}

#Main{height:95vh;background:linear-gradient(#1e3c72, #2a5298);display:flex;justify-content:center;align-items:center}#Main div .question{color:#fff;font-size:30px;font-family:'Crete Round', serif;width:90%;margin:auto;text-align:center}#Main div .options{margin-top:50px;text-align:center}#Main div .options button{margin-left:15px;margin-right:15px;padding:10px 25px;border:none;box-shadow:rgba(0,0,0,0.233) 3px 3px 8px;border-radius:5px;font-weight:bold;color:#fff}#Main div .options .no{background-color:#45CE30}#Main div .options .yes{background-color:#E5B143}.hide{-webkit-animation:myAni 1s;animation:myAni 1s}.result{color:rgba(255,255,255,0.657);font-size:30px;font-family:'Crete Round', serif;width:90%;margin:auto;text-align:center}@-webkit-keyframes myAni{0%{opacity:0}100%{opacity:1}}@keyframes myAni{0%{opacity:0}100%{opacity:1}}

